import React, { useState } from 'react'
import SingleColor from './SingleColor'
import A from './Just'
import Values from 'values.js'
import {SketchPicker,SliderPicker,CirclePicker} from 'react-color'
import logo1 from "./assets/linkedin (7).png"
import logo2 from "./assets/github (4).png"
import logo3 from "./assets/instagram (2).png"



function App() {
  const [color, setColor] = useState('')
  const [error, setError] = useState(false)
  const [list, setList] = useState(new Values('#f15025').all(5))

  const handleSubmit = (e) => {
    e.preventDefault()
    try {
      let colors = new Values(color).all(5)
      setList(colors)
    } catch (error) {
      setError(true)
      console.log(error)
    }
  }

  return (
    <>
    <header className="header">
      <section className='just2' >
        
        <div class="title">
 <h1>Color generator</h1>

</div>
        
      </section >
    </header>

        
  <section className="just2"> 
        <SliderPicker className="just2"
        color={ color }
        onChangeComplete={ (color) => {setColor(color.hex)}}
      />
      </section>


      <h3 className="now">you picked: {color}</h3>


        <section className="just2">
        <form onSubmit={handleSubmit}>
          <input
            type='text'
            value={color}
            onChange={(e) => setColor(e.target.value)}
            placeholder='#f15025'
            className={`${error ? 'error' : null}`}
          />
          <button className='btn' type='submit'>
            submit
          </button>
        </form>
      </section>
        

      
      <section className='colors '>
        {list.map((color, index) => {
          return (
            <SingleColor
              key={index}
              {...color}
              index={index}
              hexColor={color.hex}
            />
          )
        })}
      </section>


      <footer className="footer">

        <div className="footer-div" >

        <a href="https://www.linkedin.com/in/akash-patel-7a96a8152/"><img className="footer-icon" src={logo1} alt="" /></a>
        <a href="https://github.com/AkashPatel18"><img className="footer-icon" src={logo2} alt="" /></a>
        <a href="https://www.instagram.com/_akash_18/"><img className="footer-icon" src={logo3} alt="" /></a>

        </div>
       

      </footer>
    </>
  )
}

export default App
